import React from "react";
import {
  Box,
  Drawer,
  List,
  Divider,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
  Avatar,
  Button,
} from "@mui/material";

import PersonSearchIcon from "@mui/icons-material/PersonSearch";
import WorkIcon from "@mui/icons-material/Work";
import CodeIcon from "@mui/icons-material/Code";
import StarsIcon from "@mui/icons-material/Stars";
import SchoolIcon from "@mui/icons-material/School";
import PublicIcon from "@mui/icons-material/Public";
import DownloadForOfflineIcon from "@mui/icons-material/DownloadForOffline";

import { deepOrange } from "@mui/material/colors";

const drawerWidth = 250;

function SidebarComponent() {
  return (
    <div>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            width: drawerWidth,
            boxSizing: "border-box",
          },
        }}
        variant="permanent"
        anchor="left">
        <Toolbar
          sx={{
            display: "flex",
            justifyContent: "center",
            margin: 2,
          }}>
          <Avatar
            sx={{
              bgcolor: deepOrange[500],
              height: 80,
              width: 80,
            }}
            alt="Remy Sharp"
            src="data:image/jpeg;base64,"
          />
        </Toolbar>
        <div className="block text-center mb-5 leading-none capitalize">
          <h3 className="font-medium mb-0">Muhammad Arifin Habibi</h3>
          <p className="mt-0 font-thin text-sm">website developer</p>
          <Button
            variant="outlined"
            sx={{
              textTransform: "capitalize",
              marginTop: "10px",
            }}
            color="secondary">
            <DownloadForOfflineIcon
              sx={{
                marginRight: "2px",
              }}
            />{" "}
            Resume
          </Button>
        </div>
        <Divider />
        <List>
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <PersonSearchIcon />
              </ListItemIcon>
              <ListItemText primary={"Introduce"} />
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <WorkIcon />
              </ListItemIcon>
              <ListItemText primary={"Experiences"} />
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <CodeIcon />
              </ListItemIcon>
              <ListItemText primary={"Projects"} />
            </ListItemButton>
          </ListItem>
        </List>
        <Divider />
        <List>
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <SchoolIcon />
              </ListItemIcon>
              <ListItemText primary={"Educations"} />
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <StarsIcon />
              </ListItemIcon>
              <ListItemText primary={"Certificates"} />
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <PublicIcon />
              </ListItemIcon>
              <ListItemText primary={"Media Social"} />
            </ListItemButton>
          </ListItem>
        </List>
      </Drawer>
    </div>
  );
}

export default SidebarComponent;